
html, body, #root,#App, #App>div {
  width:fit-content;
 height: 100%
}
.explanation {
  font-size: larger;
  word-wrap: break-word;
    max-height: 300px;
    overflow: auto;
}

#legendDiv,#growthRateLegendDiv,#projLegendDiv,#maxVelLegendDiv,#tampPLegendDiv,#tampTLegendDiv {
  padding: 5px;
}




.dygraph-legend-line,.dygraph-legend-dash {
  display: inline-block;
  position: relative;
  top: 1ex;
  padding-left: 1em;
  height: 5px;
  border-bottom-width: 4px;
  border-bottom-style: solid;
  left: 2%;
}

.containerLegend {
  color: white;
  background: #2c2e35;
}



.footer{
  height: 50px;
  width: 100%;
  background:#2c2e35;
  color:#FFF;
  text-align:center;
  position:absolute;
  bottom:0
}


.generalTabs{
  position: fixed;
  width: 100%;
  z-index: 999;
  background: #FFF;
}
